
.n-container {
  max-width: 1150px;
  margin: auto;
  width: 90%;
}

.cient_bg {
  background-image: url('./images/Decor-1-11.png');
  background-size: 100% 100%;
}

.cryptow thead tr th {
  @apply py-4 px-1;
}

.cryptow tbody tr td {
  @apply py-2 px-1;
}

.qutbg {
  background-image: url('./images/Rectangle_194.png');
  background-size: 100% 100%;
  background-color: (0, 0, 0);
}

.search_bg {
  background-image: url('./images/Rectangle_183.png');
  background-size: 100% 100%;
}

.gradient_bg {
  background: linear-gradient(90deg, #D47A09 0%, #EBBF33 100%);
}

.login_bg {
  background-image: url('./images/Register_Page_1.png');
  background-size: 100% 100%;
  height: 100vh;
  width: 100%;

}